import React from "react";
import BaseButton from "./baseButton";
import "./button.css";
import { FaShareAlt } from 'react-icons/fa';


function ShareButton({title="", onClick=() => {}, style={}, IconComponent = () => null}) {
    return <BaseButton style={style} classNames={'button primary button-with-icon'} onClick={onClick}>
            <div>
                {title}
            </div>
            <div>
                <FaShareAlt />
            </div>
    </BaseButton>
}   

export default ShareButton;